@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

html,
body {
  overflow-x: hidden;
}

a:hover {
  text-decoration: none;
}

.services {
    /* background-image: url("../../../images/footerBg.png"); */
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding-top: 6em;
    padding-bottom: 6em;
  }
  
  .services h5 {
    color: #10bad4;
    font-weight: 500;
    text-align: center;
  }
  
  .services h3 {
    text-align: center;
    color: #404655;
    font-weight: 600;
    margin-top: 1rem;
    position: relative;
  }
  
  .services h3::before {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -20px;
    width: 6rem;
    height: 3px;
    background: rgba(16, 186, 212, 0.5);
    border-radius: 20px;
    transform: translateX(-50%);
  }
.card-container {
  border-radius: 25px;
  box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.1);
  height: 450px;
  transition: 0.3s;
}

.card-container:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.card-container img {
  height: 100px;
  width: 100px;
  object-fit: contain;
  margin: auto;
}

.card-container p {
  color: #404655;
  overflow: auto;
  height: 130px;
  text-align: center;
  margin: auto;
}

.card-footer {
  background-color: #fff;
}

